<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
    <router-view  v-if="isRouterAlive"></router-view>
  </div>
</template>

<script>
export default {
    name: 'App',
    data () {
        return {
            isRouterAlive: true
        }
    },
    provide(){ //提供
        return {
            reload: this.reload
        }
    },
    methods: {
        reload(){
            this.isRouterAlive = false
            this.$nextTick( function () {
                this.isRouterAlive = true
            })
        }
    }
}
</script>

<style>
*{
 padding: 0;
 margin: 0;
}
html,body{
 width: 100%;
 height: 100%;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* text-align: center; */
  color: #2c3e50;
  /* margin-top: 60px; */
  height: 100%;
}
</style>



