import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name:'layout',
      component: () => import('../page/layout'),
      // 嵌套路由
      children: [
        {
          // 这里不设置值，是把main作为默认页面
          path: '/', 
          name: 'archives',
          component: () => import('../page/archives')
        },
        //档案管理
        {
          path: '/archives',
          name: 'archives',
          component: () => import('../page/archives')
        },
         //儿童体检
        {
          path: '/childMedical',
          name: 'childMedical',
          component: () => import('../page/childMedical')
        },
        //测评
        {
          path: '/evaluation',
          name: 'evaluation',
          component: () => import('../page/evaluation')
        },
        //模板维护
        {
          path: '/maintenance',
          name: 'maintenance',
          component: () => import('../page/maintenance')
        },
        //专案管理
        {
          path: '/proManagement',
          name: 'proManagement',
          component: () => import('../page/proManagement'),
          children: [
             //早产儿管理
            {
              path: '/premature',
              name: 'premature',
              component: () => import('../page/proManagement/premature'),
            },
             //肥胖儿管理
            {
              path: '/obesity',
              name: 'obesity',
              component: () => import('../page/proManagement/obesity'),
            },
            //体弱儿管理
            {
              path: '/weakBaby',
              name: 'weakBaby',
              component: () => import('../page/proManagement/weakBaby'),
            },
            //其他专家管理
            {
              path: '/otherBaby',
              name: 'otherBaby',
              component: () => import('../page/proManagement/otherBaby'),
            },

          ]
        },
        //基础数据维护
        {
          path: '/basicData',
          name: 'basicData',
          component: () => import('../page/basicData')
        }
      ]
    },
  ]
})



